import React from 'react';
import like from '../img/icons8-love-16.png';
import norm from '../img/icons8-love-15.png';

function ChatList(props) {
  return(
    <div className="chat_list">
    {props.data === null ? <div>'Loading Please waite'</div> : props.data.map(item =>
      <div key={item.id} className={item.user === "Taylor" ? "top_item" : "item"}>
        <div className="avatar">
          <img src={item.avatar} alt={item.user}/>
        </div>
        <div className="item_content">
          <p className="user_name">{item.user}</p>
          <p className="created_at">{item.created_at}</p>
          <span className="control_message">
            {item.editMode ?
              <input
                className="input_edit_mode"
                value={props.editInputValue}
                onChange={(event) => props.getInputValueEditMode(event)}
                onKeyPress={(event) => props.inputEditModeKeyPress(event, item.id)}
                /> :
               <span className="user_message">{item.message}</span>
             }
             <div className="edit_mode">
               {item.user === "Taylor"?
                 <span
                 onClick={() => props.enableEditMode(item.id, item.message)}>
                 Edit
                 </span> : ''
               }
               {item.user === "Taylor"? <span onClick={() => props.deleteMessage(item.id)}>Delete</span> : ''}
             </div>
          </span>
          <div className="like_container">
            {item.user === "Taylor" ? '':
              <p onClick={() => props.toggleLike(item.id)}>{item.like ? <img src={like}/> : <img src={norm}/>}</p>
            }
          </div>
        </div>
      </div>
    )}
    </div>
  )
}

export default ChatList;
