import React, { Component } from 'react';

import Header from './Header';
import ChatList from './ChatList';
import MessageInput from './MessageInput';
import Footer from './Footer';

class MainChat extends Component{
  componentWillMount() {
    this.props.loadData()
  }

  render (props) {
    console.log(this.props.data)
    return (
      <div className="wrapper">
      <div className="content">
        <Header
          usersCount={this.props.usersCount}
          messagesCount={this.props.messagesCount}
          lastMessageTime={this.props.lastMessageTime}
        />
        <ChatList
          data={this.props.data}
          toggleLike={this.props.toggleLike}
          deleteMessage={this.props.deleteMessage}
          enableEditMode={this.props.enableEditMode}
          editInputValue={this.props.editInputValue}
          getInputValueEditMode={this.props.getInputValueEditMode}
          inputEditModeKeyPress={this.props.inputEditModeKeyPress}
        />
        <MessageInput
          getInputValue={this.props.getInputValue}
          sendMessage={this.props.sendMessage}
          inputValue={this.props.inputValue}
        />
      </div>
      <Footer />
      </div>
    )
  }
}

export default MainChat;
