import React from 'react';

function Header(props) {
  return(
    <header>
      <div className="header_logo_container">
        <div className="logo">
          <a href="#">My Chat</a>
        </div>
        <div className="header_info">
          {props.usersCount === null ? <div>0</div> : <div>{props.usersCount} users</div>}
          {props.messagesCount === null ? <div>0</div> : <div>{props.messagesCount} messages</div>}
        </div>
      </div>
      <div className="time_block">
        Last message: {props.lastMessageTime}
      </div>
    </header>
  )
}

export default Header;
