import { DISPLAY_DATA,
        GET_INPUT_VALUE,
        SEND_MESSAGE,
        TOGGLE_LIKE,
        DELETE_MESSAGE,
        ENABLE_EDIT_MODE,
        GET_INPUT_VALUE_EDIT_MODE,
        INPUT_EDIT_MODE_KEY_PRESS
      } from './actions';

const initialState = {
  data: null,
  usersCount: null,
  messagesCount: null,
  lastMessageTime: null,
  inputValue: [],
  editInputValue: [],
}

const helpFunction = {
  usersCount: function (data) {
    let users = data.map(item => {
      return item.user;
    });
    let filtredUsers = [];
    for (let i of users) {
      if (filtredUsers.length === 0 ) {
        filtredUsers.push(i);
      }else {
        let user = filtredUsers.find(item=> i === item);
        if (user === undefined) {
          filtredUsers.push(i);
        }
      }
    }
    return filtredUsers.length;
  },
  messagesCount: function (data) {
    let messages = data.map(item => {
      return item.message;
    })
    return messages.length;
  },
  lastMessageTime: function (data) {
    let lastMessageTime = data.map(item => {
      return item.created_at;
    }).sort();
    return lastMessageTime[lastMessageTime.length -1];
  },
  getId: function (num){
    let id = [];
    let str = '1234567890';
    for (let i = 0; i < num; i++) {
      let randomIndex = Math.floor(Math.random() * 10);
      id.push(str[randomIndex]);
    }
    return id.join('');
  },
  getMessageTime: function () {
    let today = new Date();
    let date = today.getFullYear()+'-'+(today.getMonth()+1)+'-'+today.getDate();
    let time = today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
    let dateTime = date+' '+time;
    return dateTime;
  },
};

export default function mainChatReducer(state = initialState, action) {
  switch (action.type) {
    case DISPLAY_DATA:
      return {
        ...state,
        data: action.data,
        usersCount: helpFunction.usersCount(action.data),
        messagesCount: helpFunction.messagesCount(action.data),
        lastMessageTime: helpFunction.lastMessageTime(action.data),
      };
    case GET_INPUT_VALUE:
      return {
        ...state,
        inputValue: action.value,
      };
    case SEND_MESSAGE:
      action.event.preventDefault();
      let myMessage = {
        id: helpFunction.getId(13),
        user: "Taylor",
        avatar:"https://i.pravatar.cc/300?img=12",
        created_at: helpFunction.getMessageTime(),
        message: `${state.inputValue}`,
      };
      return {
        ...state,
        data: [...state.data, myMessage],
        inputValue: [],
        usersCount: helpFunction.usersCount([...state.data, myMessage]),
        messagesCount: helpFunction.messagesCount([...state.data, myMessage]),
        lastMessageTime: helpFunction.lastMessageTime([...state.data, myMessage]),
      };
    case TOGGLE_LIKE:
    let editedData = state.data.map(item => {
      if (item.id === action.id) {
         if (item.like === false) {
           item.like = true;
           return item;
         }
         if (item.like === true) {
           item.like = false;
           return item;
         }
      }
      return item;
    })
      return {
        ...state,
        data: editedData,
      };
    case DELETE_MESSAGE:
    let filtredData = state.data.filter(item => item.id !== action.id);
      return {
        ...state,
        data: filtredData,
        messagesCount: helpFunction.messagesCount(filtredData),
        lastMessageTime: helpFunction.lastMessageTime(filtredData),
      };
    case ENABLE_EDIT_MODE:
      let editedEditModeData = state.data.map(item => {
        if (item.id === action.id) {
          item.editMode = true;
          return item;
        }
        return item;
      })
      return {
        ...state,
        data: editedEditModeData,
        editInputValue: action.value,
      };
    case GET_INPUT_VALUE_EDIT_MODE:
      return {
        ...state,
        editInputValue: action.value,
      };
    case INPUT_EDIT_MODE_KEY_PRESS:
      let editedDataEditModeInput = state.data;
      if (action.event.key === 'Enter' && state.editInputValue !== '') {
        editedDataEditModeInput = state.data.map(item => {
          if (item.id === action.id) {
            item.message = state.editInputValue;
            item.editMode = false;
            return item;
          }
          return item;
        })
      };
      return {
        ...state,
        data: editedDataEditModeInput,
      };
    default:
      return state;
  }
}
