import { connect } from 'react-redux';
import MainChat from './MainChat';
import {
  loadData,
  getInputValue,
  sendMessage,
  toggleLike,
  deleteMessage,
  enableEditMode,
  getInputValueEditMode,
  inputEditModeKeyPress
} from '../redux/actions';

function mapStateToProps(state) {
  return {
    data: state.mainChat.data,
    usersCount: state.mainChat.usersCount,
    messagesCount: state.mainChat.messagesCount,
    lastMessageTime: state.mainChat.lastMessageTime,
    inputValue: state.mainChat.inputValue,
    editInputValue: state.mainChat.editInputValue,
  }
}


function mapDispatchToProps(dispatch) {
  return {
    loadData: () => dispatch(loadData()),
    getInputValue: (event) => dispatch(getInputValue(event.target.value)),
    sendMessage: (event) => dispatch(sendMessage(event)),
    toggleLike: (id) => dispatch(toggleLike(id)),
    deleteMessage: (id) => dispatch(deleteMessage(id)),
    enableEditMode: (id, value) => dispatch(enableEditMode(id, value)),
    getInputValueEditMode: (event) => dispatch(getInputValueEditMode(event.target.value)),
    inputEditModeKeyPress: (event, id) => dispatch(inputEditModeKeyPress(event, id)),
  }
}


const MainChatContainer = connect(mapStateToProps, mapDispatchToProps)(MainChat);
export default MainChatContainer;
