export const DISPLAY_DATA = 'display_data';
export const GET_INPUT_VALUE = 'get_input_value';
export const SEND_MESSAGE = 'send_message';
export const TOGGLE_LIKE = 'toggle_like';
export const DELETE_MESSAGE = 'delete_message';
export const ENABLE_EDIT_MODE = 'enable_edit_mode';
export const GET_INPUT_VALUE_EDIT_MODE = 'get_input_value_edit_mode';
export const INPUT_EDIT_MODE_KEY_PRESS = 'input_edit_mode_key_press';

export function loadData() {
  return (dispatch) => {
    fetch('https://api.dev.buki.com.ua/frontend_test')
      .then(res => res.json())
      .then(data => {
        let myData = data.map(item => {
          return {...item,like: false, editMode: false}
        })
        dispatch(displayData(myData));
      })
  };
}

export function displayData(data) {
  return {
    type: DISPLAY_DATA,
    data
  };
}

export function getInputValue(value) {
  return {
    type: GET_INPUT_VALUE,
    value
  };
}

export function sendMessage(event) {
  return {
    type: SEND_MESSAGE,
    event
  };
}

export function toggleLike(id) {
  return {
    type: TOGGLE_LIKE,
    id
  }
}

export function deleteMessage(id) {
  return {
    type: DELETE_MESSAGE,
    id
  }
}

export function enableEditMode(id, value) {
  return {
    type: ENABLE_EDIT_MODE,
    id,
    value
  }
}

export function getInputValueEditMode(value) {
  return {
    type: GET_INPUT_VALUE_EDIT_MODE,
    value
  }
}

export function inputEditModeKeyPress(event, id) {
  return {
    type: INPUT_EDIT_MODE_KEY_PRESS,
    event,
    id,
  }
}
